.cards {
  margin-left: 2rem;
  display: flex;
  gap: 2rem;
}

#rv .card {
  width: 10rem;
  text-align: center;
  padding: 3.5rem;
  border-radius: 1rem;
  box-shadow: 1px 1px 1px 1px grey;
  background: gray;
}

#rv .tc {
  color: white;
}

#rv .dt {
  display: none;
}
#rv:hover .dt {
  display: block;
}
.ac {
  cursor: pointer;
  opacity: 1;
}
